  /* NAVBAR */
  .navbar {
	position: fixed;
	width: 100%;
	height: 60px;
	padding: 20px;
	-webkit-box-sizing: border-box;
			box-sizing: border-box;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background-color: white;
	-webkit-box-pack: justify;
		-ms-flex-pack: justify;
			justify-content: space-between;
	z-index: 1000;
  }
  
  /* NAVBAR */
  /* NAVBAR TOP */
  .navbar .logo {
	font-size: 18px;
	padding: 5px;
	-webkit-box-sizing: border-box;
			box-sizing: border-box;
	color: white;
	background: #9e5d9e;
	border-radius: 8px;
	text-decoration: none;
	-webkit-transition: 0.8s;
	transition: 0.8s;
  }
  
  .navbar .logo:hover {
	color: purple;
	background: white;
  }
  
  /* NAVBAR */
  /* TOGGLE */
  .navbar #toggle {
	width: 25px;
	height: 25px;
	cursor: pointer;
	position: relative;
  }
  
  .navbar #toggle:before {
	content: '';
	position: absolute;
	top: 5px;
	width: 100%;
	height: 4px;
	background: purple;
  }
  
  .navbar #toggle:after {
	content: '';
	position: absolute;
	top: 13px;
	width: 100%;
	height: 4px;
	background: purple;
  }
  
  .navbar #toggle.active:before {
	background: #cf52cf;
  }
  
  .navbar #toggle.active:after {
	background: #cf52cf;
  }
  
  /* NAVBAR */
  /* NAVIGATION MENU */
  #navigate {
	position: fixed;
	top: 60px;
	right: 0;
	width: 20%;
	height: 100vh;
	background: #020024;
	background: -webkit-gradient(linear, left bottom, left top, from(#020024), color-stop(0%, rgba(160, 44, 170, 0.75674)), color-stop(84%, #ffffff));
	background: linear-gradient(0deg, #020024 0%, rgba(160, 44, 170, 0.75674) 0%, #ffffff 84%);
	z-index: 1;
	-webkit-transition: 0.8s;
	transition: 0.8s;
  }
  
  #navigate img {
	width: 18px;
	height: 18px;
  }
  
  #navigate ul li {
	list-style: none;
	padding: 10px;
	-webkit-box-sizing: border-box;
			box-sizing: border-box;
  }
  
  #navigate ul li a {
	color: black;
	font-size: 20px;
	text-decoration: none;
  }
  
  #navigate ul li a:hover {
	color: purple;
  }
  
  #navigate.active {
	right: -20%;
	-webkit-transition: 0.8s;
	transition: 0.8s;
  }
  
